import { GetByDeviceId, deleteDevice } from '@/api/parts';
import Details from "./details";
import AddOther from "./addOther";
export default {
  name: 'PartDevice',
  data: function data() {
    return {
      loading: false,
      tableData: [],
      code: ''
    };
  },
  components: {
    Details: Details,
    AddOther: AddOther
  },
  created: function created() {
    this.init();
  },
  methods: {
    init: function init() {
      var _this = this;

      this.loading = true;
      this.code = this.$route.query.code;
      GetByDeviceId({
        deviceId: this.$route.query.id
      }).then(function (res) {
        _this.loading = false;
        _this.tableData = res.data;
      });
    },
    edit: function edit(row) {
      this.$refs.details.init(row);
    },
    del: function del(id) {
      var _this2 = this;

      this.$confirm('此操作将永久删除该项, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(function () {
        deleteDevice({
          id: id
        }).then(function () {
          _this2.$message({
            type: 'success',
            message: '删除成功',
            duration: 500,
            onClose: function onClose() {
              _this2.pageIndex = 1;

              _this2.init();
            }
          });
        });
      }).catch(function () {
        _this2.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    add: function add() {
      this.$refs.details.init();
    },
    insertOther: function insertOther() {
      this.$refs.addOther.dialogVisible = true;
    }
  }
};